<template>
  <div v-if="$auth.isAuthenticated">
    <div class="profile">
      <div class="">
        <h4>{{ $auth.user.name }}</h4>
        <small>{{ $auth.user.email }}</small>
      </div>
      <img :src="$auth.user.picture">
      <v-btn style="margin: 0 12px" v-if="$auth.isAuthenticated" @click="logout" depressed small>Log out</v-btn>
    </div>
    <!-- <div>
      <pre>{{ JSON.stringify($auth.user, null, 2) }}</pre>
    </div> -->
  </div>
  <div v-else class="">
    <v-btn style="margin: 0 12px" @click="login">login</v-btn>
  </div>
</template>

<script>

export default {
  methods: {
    login() {
      this.$auth.loginWithRedirect()
    },
    logout() {
      this.$auth.logout({
        returnTo: window.location.origin
      })
    }
  }
}

</script>


