import Vue from 'vue'
import Vuex from 'vuex'
import moment from 'moment'


Vue.use(Vuex)


function toUnix(arg){
  const n = Date.now()
  let s = moment()
  if ( arg === 'daily')
    s = s.add(1, 'day')
  else if ( arg === 'weekly' )
    s = s.add(1,'week')
  else if ( arg === 'mountly')
    s = s.add(1,'M')
  else if ( arg === 'quarterly')
    s = s.add(3, 'M')
  else if ( arg === 'yearly' )
    s = s.add(1, 'Y')
  else
    s = s.add(1,'day')

  return s.format('x') - n
}

function normArray(arr, from , to){
  let list = {}
  for ( const { start, amount } of arr )
    list[start] = list[start] ? list[start]+ amount : amount

  return Object.keys(list).map(start => ({
    amount: list[start],
    start: parseInt(start)
  }))
  .filter(({start}) => start >= from && start <= to )
  .sort( (a, b) => a.start > b.start ? 1 : -1 )
}

export default new Vuex.Store({
  state: {
    from: Date.now(),
    to: moment().add(1, 'year').format('x'),
    events: []
  },
  mutations: {
    select(state, { from , to }){
      state.from = new Date(from).getTime()
      state.to = new Date(to).getTime()
    },
    remove(state, i){
      state.events.splice(i,1)
    },
    addtool(state, { name, children, start }){
      state.events.unshift({
        type: 'tool',
        name,
        children,
        start: new Date(start).getTime(),
        display: true
      })
    },
    editevent( state, { index, key, value }){
      state.events = state.events.map( (item, i) => i === index ? {
        ...item,
        [key]: value
      } : item )
    },
    addevent(state, {name , amount, start, repeat}){
      state.events.unshift({
        type: 'simple',
        name ,
        repeat ,
        amount: parseInt(amount),
        start: new Date(start).getTime(),
        display: true
      })
    },
    display(state, i){
      state.events = state.events.map((item, index) => ({
        ...item,
        display: i === index ? !item.display : item.display
      }))
    }
  },
  getters: {
    repeats(){
      return [
        'daily',
        'weekly',
        'mountly',
        'quarterly',
        'yearly'
      ]
    },
    series({events, from, to}){
      let dates = events
        .filter(({type, display }) => type === 'simple' && display )
        .sort( (a, b) => a.start > b.start ? 1 : -1 )

      const parents = dates.filter(({repeat}) => repeat)

      for ( const parent of parents ){
        let children = []

        while ( !children.length || children[children.length - 1].start < to ) {
          children.push({
            amount: parent.amount,
            start: parent.start + toUnix(parent.repeat) * (children.length+1)
          })
        }

        dates = [
          ...dates,
          ...children
        ]

      }

      for (
        const tool of events.filter( ({type, display}) => type === 'tool' && display )
      ) {
        dates = [
          ...dates,
          ...Array.from(tool.children)
        ]
      }


      let income = normArray(dates.filter(({amount}) => amount > 0 ), from , to)
      let costs = normArray(dates.filter(({amount}) => amount < 0 ), from , to)

      return [{
              name: "Остаток",
              // dont remove prev amount and calc it in full time
              data: normArray(dates, new Date('1970-01-01').getTime() , to).reduce((acc, {amount, start}) => ([ ...acc,
                  acc.length ? [start, acc[acc.length -1 ][1] + amount ] : [start, amount]
              ]), []).filter(([start]) => start >= from && start <= to )
            },
            {
              name: "Приход",
              data: income.map(({amount, start}) => ([start, amount]))
            },
            {
              name: 'Расход',
              data: costs.map(({amount, start}) => ([start, Math.abs(amount)]))
            }
          ]
    }
  }
})
